import Koa from 'koa'
import koaBody from 'koa-body'
import koaLogger from 'koa-logger'
import config from './config'
import routers from './router'
import apolloServer from './graphql'
import error from './error'
import jwt from 'koa-jwt'
import { logInfo } from './log'
import { connectMongoDB } from './database'
import jwtErrorHandle from './middleware/jwt/jwtErrorHandle'

const app = new Koa()

// „Éá„Éº„Çø„Éô„Éº„ÇπËµ∑Âãï
connectMongoDB()

// jwtË™çË®º
app.use(jwtErrorHandle)
app.use(
  jwt({
    secret: config.secret,
  }).unless({
    path: [/\/graphql/, /\/auth/, /\/data/, /\/form/],
  }),
)

// „Éü„Éâ„É´„Ç¶„Çß„Ç¢
app.use(koaLogger())
app.use(koaBody())
app.use(routers.routes()).use(routers.allowedMethods())

// „Ç®„É©„Éº„ÄÅ„É≠„Ç∞Âá¶ÁêÜ
app.on('error', error)
app.on('log', logInfo)

// Apollo GraphqlËµ∑Âãï
apolloServer(app)

// „Çµ„Éº„ÉêËµ∑Âãï„Å®Â∞éÂá∫
export default app.listen(config.host, async (): Promise<void> => {
  console.log(`server running on port ${config.host} üöÄ`)
  console.log(process.env.NODE_ENV === 'development' ? 'Rrea-server dev üí™' : 'Rrea-server„Å∏„Çà„ÅÜ„Åì„ÅùÔºÅü§£')
  process.env.NODE_ENV === 'development' || logInfo('production server start!')
})
