import dayjs from 'dayjs'
import config from '../config'
import CryptoJS from 'crypto-js'
import auths from '../models/auth.model'
import jsonwebtoken from 'jsonwebtoken'
import type { IAuth, IAuthResponse } from '../types/auth.type'
import type { ICTXPost, ICTXPut, ICTXDelete, ICTXGet } from '../types/ctx.type'

// ----- ÁÆ°ÁêÜËÄÖË™çË®ºÈñ¢‰øÇ controller -----

/**
 *  ÊöóÂè∑Ëß£Ë™≠
 *  @param {string} word ÊöóÂè∑Âåñ„Åó„Åü„Éë„Çπ„ÉØ„Éº„Éâ
 *  @return {string} „Éë„Çπ„ÉØ„Éº„Éâ
 */

const decrypt = (word: string) => {
  const key = CryptoJS.enc.Utf8.parse(config.passwordKey)
  const iv = CryptoJS.enc.Utf8.parse(config.passwordIV)
  const encryptedHexStr = CryptoJS.enc.Hex.parse(word)
  const srcs = CryptoJS.enc.Base64.stringify(encryptedHexStr)
  const decrypt = CryptoJS.AES.decrypt(srcs, key, { iv: iv, mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7 })
  const decryptedStr = decrypt.toString(CryptoJS.enc.Utf8)
  return decryptedStr.toString()
}

/**
 *  „É≠„Ç∞„Ç§„É≥
 *  @param {ICTXPost<IAuth, IAuth | ''>} ctx koa„Ç≥„É≥„ÉÜ„É≥„ÉÑ
 */

const login = async (ctx: ICTXPost<IAuth, IAuth | ''>): Promise<void> => {
  // ÊöóÂè∑Ëß£Ë™≠
  const password: string = await decrypt(ctx.request.body!.passWord)

  await auths
    .find({ id: ctx.request.body?.id }, { _id: 0 })
    .then((res): void => {
      // ÁÑ°Âäπ„Å™ID
      if (res.length === 0) {
        ctx.body = {
          code: 10002,
          message: 'id„ÅØÂ≠òÂú®„Åó„Å™„ÅÑ„Åß„Åô',
          result: '',
        }

        return
      }

      // „É≠„Ç∞„Ç§„É≥ÊàêÂäü
      if (password === res[0].passWord) {
        // token‰ΩúÊàê
        const token: string = jsonwebtoken.sign(
          {
            id: res[0].id,
            permission: res[0].permission,
            exp: config.jwtLimitTime,
          },
          config.secret,
        )

        ctx.body = {
          code: 10001,
          message: '„É≠„Ç∞„Ç§„É≥ÊàêÂäü„Åó„Åæ„Åó„Åü',
          result: {
            id: res[0].id,
            passWord: '*****',
            permission: res[0].permission,
            time: res[0].time,
            token: token,
          },
        }

        ctx.app.emit('log', `„É≠„Ç∞„Ç§„É≥--${ctx.request.body?.passWord}`)
        auths.updateOne({ id: res[0].id }, { time: dayjs().format('YYYY-MM-DD HH:mm:ss'), token: token }).then()
      } else {
        // ÁÑ°Âäπ„Å™„Éë„Çπ„ÉØ„Éº„Éâ
        ctx.body = {
          code: 10003,
          message: '„Éë„Çπ„ÉØ„Éº„Éâ„ÅåÈñìÈÅï„ÅÑ„Åæ„Åó„Åü',
          result: '',
        }
      }
    })
    .catch((): void => {
      ctx.app.emit('error', 10004, ctx)
    })
}

/**
 *  „É≠„Ç∞„Ç¢„Ç¶„Éà
 *  @param {ICTXDelete<'id', ''>} ctx koa„Ç≥„É≥„ÉÜ„É≥„ÉÑ
 */

const logout = async (ctx: ICTXDelete<'id', ''>): Promise<void> => {
  await auths
    .updateOne({ id: ctx.params.id }, { token: '' })
    .then(() => {
      console.log(`${ctx.params.id} is Logout`)
      ctx.app.emit('log', `„É≠„Ç∞„Ç¢„Ç¶„Éà--${ctx.params.id}`)

      ctx.body = {
        code: 10008,
        message: '„É≠„Ç∞„Ç¢„Ç¶„ÉàÊàêÂäü„Åó„Åæ„Åó„Åü',
        result: '',
      }
    })
    .catch((): void => {
      ctx.app.emit('error', 10004, ctx)
    })
}

/**
 *  „Åô„Åπ„Å¶„ÅÆÁÆ°ÁêÜËÄÖÊÉÖÂ†±ÂèñÂæó
 *  @param {ICTXGet<{}, IAuthResponse>} ctx koa„Ç≥„É≥„ÉÜ„É≥„ÉÑ
 */

const authFind = async (ctx: ICTXGet<{}, IAuthResponse>): Promise<void> => {
  ctx.app.emit('log', 'ÁÆ°ÁêÜËÄÖÊÉÖÂ†±ÂèñÂæó')

  await auths
    .find({}, { _id: 0, passWord: 0 })
    .then((res): void => {
      ctx.body = {
        code: 10000,
        message: 'ÁÆ°ÁêÜËÄÖÊÉÖÂ†±ÂèñÂæóÊàêÂäü',
        result: res,
      }
    })
    .catch((): void => {
      ctx.app.emit('error', 10006, ctx)
    })
}

/**
 *  ÁÆ°ÁêÜËÄÖ„ÅÆ„Éë„Çπ„ÉØ„Éº„Éâ‰øÆÊ≠£
 *  @param {ICTXPut<'id', Record<'passWord', string>, ''>} ctx koa„Ç≥„É≥„ÉÜ„É≥„ÉÑ
 */

const authUpdate = async (ctx: ICTXPut<'id', Record<'passWord', string>, ''>): Promise<void> => {
  const password: string = await decrypt(ctx.request.body!.passWord)
  ctx.app.emit('log', 'ÁÆ°ÁêÜËÄÖ„ÅÆ„Éë„Çπ„ÉØ„Éº„Éâ‰øÆÊ≠£')

  await auths
    .updateOne({ id: ctx.params.id }, { passWord: password })
    .then((): void => {
      ctx.body = {
        code: 10000,
        message: 'ÁÆ°ÁêÜËÄÖ„ÅÆ„Éë„Çπ„ÉØ„Éº„Éâ‰øÆÊ≠£ÊàêÂäü',
        result: '',
      }
    })
    .catch((): void => {
      ctx.app.emit('error', 10007, ctx)
    })
}

export default { login, logout, authFind, authUpdate }
