import Log from 'log4js'

// ----- „Çµ„Éº„Éê„Éº„É≠„Ç∞Âá¶ÁêÜ -----
// log„Çµ„Ç§„Ç∫
const max: number = 10000

// log4js„Ç≥„É≥„Éï„Ç£„Ç∞
Log.configure({
  appenders: {
    info: {
      type: 'dateFile',
      filename: 'logs/info.log',
      pattern: 'yyyy-MM-dd.log',
      encoding: 'utf-8',
      maxLogSize: max,
    },
    error: {
      type: 'dateFile',
      filename: 'logs/error.log',
      pattern: 'yyyy-MM-dd.log',
      encoding: 'utf-8',
      maxLogSize: max,
    },
  },

  categories: {
    default: { appenders: ['info'], level: 'info' },
    info: { appenders: ['info'], level: 'info' },
    error: { appenders: ['error'], level: 'error' },
  },
})

/**
 *  „Ç®„É©„Éº„É≠„Ç∞Âá¶ÁêÜ
 *  @param {string} content „É≠„Ç∞„ÅÆÂÜÖÂÆπ
 */

const logError = (content: string): void => {
  const log = Log.getLogger('error')
  log.error(content)
}

/**
 *  ‰∏ÄËà¨ÁöÑ„Å™„É≠„Ç∞Âá¶ÁêÜ
 *  @param {string} content „É≠„Ç∞„ÅÆÂÜÖÂÆπ
 */

const logInfo = (content: string): void => {
  const log = Log.getLogger('info')
  log.info(content)
}

export { logError, logInfo }
